@import "https://www.nerdfonts.com/assets/css/webfont.css";
@import url("https://fonts.googleapis.com/css2?family=Cherry+Cream+Soda&family=Lexend+Deca:wght@100..900&family=Noto+Sans:ital,wght@0,100..900;1,100..900&family=Rock+Salt&display=swap");

/*
Lexend Deca
Rock Salt
Noto Sans
(Cherry Cream Soda)
*/

:root {
  --prime: #000;
  --second: #fefae0;
  --accent: #415a77;
  --cold: #11dfff;
  --warm: #ea5454;
  --alert: #fca311;
}

::selection {
  color: var(--prime);
  background: var(--alert);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  height: 100%;
  width: 100%;
}

body {
  font-size: 18px;
  color: var(--prime);
  background-color: var(--second);
  font-family: "Noto Sans", sans-serif;
}

/*main styling*/

h1 {
  display: inline-block;
  background-color: var(--accent);
  color: var(--second);
  padding: 2rem;
  width: 100%;
  text-align: center;
  font-family: "Lexend Deca", sans-serif;
}

main {
  padding: 2rem;
}

form {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

form input {
  border: none;
  outline: none;
  background-color: var(--accent);
  color: var(--second);
  font-size: 1.2rem;
  border-radius: 3rem;
  padding: 0.5rem 1.4rem;
  height: 3rem;
  width: 20rem;
}

form button {
  border: none;
  font-size: 1.2rem;
  padding: 0.5rem 1.4rem;
  background-color: var(--accent);
  color: var(--second);
  border-radius: 3rem;
  height: 3rem;
  width: 7rem;
  text-align: center;
}

form button:hover {
  text-decoration: underline;
}

#weather {
  margin: 1rem;
}

.city-temp {
  text-align: center;
  font-size: 3rem;
  font-family: "Lexend Deca", sans-serif;
}

#fullplace,
#coords {
  text-align: center;
  margin: 1rem;
}

#fullplace {
  font-style: italic;
}

h3 {
  font-size: 2rem;
  margin: 3rem 0;
  font-family: "Lexend Deca", sans-serif;
}

/*alerts*/

#alerts {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

#alerts > * {
  background-color: var(--alert);
  padding: 1rem 2rem;
  border-radius: 2rem;
  width: 75%;
}

/*future conditions*/

#next-days {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  column-gap: 2rem;
  row-gap: 2rem;
}

#next-days > * {
  text-align: center;
}

#next-days i {
  font-size: 5rem;
}

.day {
  font-size: 1.2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  background-color: var(--accent);
  color: var(--second);
  padding: 2rem;
  border-radius: 2rem;
}

.day p,
.day div {
  background-color: var(--second);
  color: var(--accent);
  padding: 0.8rem;
  width: 15rem;
  border-radius: 2rem;
}

.day .min-temp {
  color: var(--cold);
}

.day .max-temp {
  color: var(--warm);
}

/*current conditions*/

.table-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

table#current-table {
  width: 75%;
}

table#current-table {
  border-collapse: separate;
  border-spacing: 0 0.5rem;
}

table#current-table td,
table#current-table th {
  padding: 0.8rem;
  border: solid 0.2rem var(--accent);
}

table#current-table th {
  background-color: var(--accent);
  color: var(--second);
  border-radius: 2rem 0 0 2rem;
  width: 25%;
}

table#current-table td {
  text-align: center;
  border-radius: 0 2rem 2rem 0;
  width: 25%;
}

table#current-table td > p {
  display: inline-block;
}

p#wind-info {
  margin: 2rem;
}

/*footer*/

footer {
  background-color: var(--alert);
  text-align: center;
  padding: 2rem;
}

footer a {
  text-decoration: underline;
  color: var(--prime);
  padding: 0.2rem 0.4rem;
  border-radius: 2rem;
  background-color: var(--second);
  transition: all 250ms ease;
}

footer a:hover {
  text-decoration: none;
  background-color: var(--accent);
  color: var(--second);
}

/*wide screens*/

@media screen and (min-width: 1800px) {
  #next-days {
    justify-content: space-evenly;
  }
}

/*medium screens*/

@media screen and (max-width: 1450px) {
  #next-days {
    justify-content: space-evenly;
  }
}

/*small screens*/

@media screen and (max-width: 512px) {
  form {
    flex-direction: column;
  }

  form input {
    text-align: center;
  }

  form button#submit {
    width: 20rem;
  }

  #weather {
    margin: 0;
  }

  h2.city-temp {
    margin-top: 1rem;
    font-size: 2.3rem;
  }

  h3 {
    text-align: center;
  }
}

@media screen and (max-width: 850px) {
  table#current-table {
    width: 100%;
  }

  table#current-table th {
    width: 50%;
  }

  table#current-table td {
    width: 50%;
  }
}
